import React from 'react';


class SearchBar extends React.Component {
    state = {term:''};

    onFormSubmit = (event) => {
        event.preventDefault();

        this.props.onSubmit(this.state.term)
    }

    render() {
        return (
            <div className="ui segment">
                <form onSubmit={this.onFormSubmit} className="ui form">
                    <div className="field">
                        <label>ImageSearch</label>
                        <input 
                        type="text" 
                        value={this.state.term} 
                        onChange={e => this.setState({term: e.target.value})} /> 
                    </div> 
                </form>
            </div>
        ); 
    };
};


export default SearchBar;

//when we pass a method(OnInputChange) to a prop like onChange, it is a function(call back function). We donot put set of paranthesis there because we donot want to run whenever this component is rendered. By not putting paranthesis we can call it sometime in future. By not putting the paranthesis we are passing a reference to this function to the input element so that input can call it sometimne in future.

